.App {
  display: flex;
  margin-top: 5vh;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
}

.wrap {
  display: flex;
}

.alert {
  position: absolute;
  bottom: 0;
}

.tree {
  width: 80vw
}
.result pre{
  height: 50vh;
  margin-left: 1vw;
  width: 38vw;
  overflow: auto;
  border: 1px solid black;
  background-color: #eeeeee;
  word-break: normal !important;
  word-wrap: normal !important;
  white-space: pre !important;
}